<template>
  <main id="app" class="container"  v-on:touchstart="start" v-on:touchend="end" v-on:touchmove="move" v-on:touchforcechange="onTouchForceChange" v-on:webkitmouseforcechanged="onTouchForceChange">
    <stage :forceValue="forceValue" ref="game" />
    <div v-if="!running" id="start-screen">
      <div class="content">
        <p>Use 3D Touch to prevent the red square from hitting the obstacles. Push red cirle below to try.</p>
        <button id="start-button" ref="start" v-on:click="startGame" v-on:touchstart="startGame">START</button>
      </div>
    </div>
    <div id="control" ref="control"></div>
    <!-- <h1>{{ forceValue | round }}</h1> -->
  </main>
</template>

<script>
import Stage from '@/components/Stage'

export default {
  name: 'App',
  components: {
    'stage': Stage
  },

  methods : {
    startGame: function(e) {
      this.running = true;
      this.$refs.game.start();
    },
    start: function(e) {
      e.preventDefault();
    },
    end: function() {
      setTimeout(this.stop.bind(this), 10);
    },
    stop: function() {
      this.forceValue = 0;
    },
    move: function (e) {
      e.preventDefault();
    },
    onTouchForceChange: function(e) {
      this.forceValue = e.changedTouches[0].force;
      var control = this.$refs.control;
      control.style.transform = "scale(" + (1 + this.forceValue*.4) + ")";
    },
    onClickForceChange: function(e) {
      this.forceValue = e.webkitForce / 3
    }
  },
  data: function () {
    return {
      forceValue: 0,
      running: false
    }
  },
  filters: {
    round: function (value) {
      return Math.round(value * 100) / 100;
    }
  }
}
</script>

<style lang="scss">
body,html,#app {
  font-family: 'Cousine', monospace;
  margin:0;
  padding:0;
  height: 100%;
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  position: relative;
}
#start-screen {
  background:rgba(0,0,0,.8);
  position:absolute;
  padding-top:100%;
  top:0;
  left:0;
  width:100%;
  z-index:10;
  .content {
    position:absolute;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
    width: 80%;
    padding:0 10%;
  }
  p {
    color:#fff;
    text-align: center;

  }
}
#start-button {

  font-family: 'Cousine', monospace;
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: block;
  font-size: 30px;
  font-weight:bold;
  margin: 0 auto;
}
#control {
  height:80px;
  width:80px;
  border-radius:100%;
  margin:30px auto 0;
  background:#d9534f;
}
</style>



