<template>
  <div id="flappy" ref="flappy">
    <div class="body">

    </div>
  </div>
</template>

<script>
export default {
  name: 'Flappy',
  props: ['flappyY'],

  mounted: function() {
    this.element = this.$refs.flappy;
    this.reportPosition()
  },

  methods:  {
    draw: function() {
      if (this.element) {
        // var y = Math.round((this.flappyY * 100 * .9)) + '%';
        var y = (90 - (this.flappyY * 100 * .9)) + '%';
        this.element.style.top = y;
        this.reportPosition();
      }
    },
    reportPosition: function(){
      var data = {
        id: 'flappy',
        position: {
          left : this.element.offsetLeft,
          top : this.element.offsetTop,
          right : this.element.offsetLeft + this.element.clientWidth,
          bottom : this.element.offsetTop + this.element.clientHeight
        }
      }
      this.$emit('currentPosition', data)
    }
  },
  data () {
    return {
      element: null
    }
  },
  watch: {
    flappyY: {
      immediate: true,
      handler: function () {
        this.draw();
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang="scss" scoped>
#flappy {
  background: #ccc;
  width: 10%;
  height: 10%;
  position: absolute;
  top:90%;
  transform-origin:left center;
  z-index:100;
  .body {
    background: #d9534f;
    position: absolute;
    height: 100%;
    width: 100%;
  }
}
</style>



