<template>
  <div ref="obstacle">
    <div class="part top" ref="top"></div>
    <div class="part bottom" ref="bottom"></div>
  </div>
</template>

<script>
export default {
  name: 'Obstacle',
  props: ['index', 'distance'],
  mounted: function (argument) {
    this.top = this.$refs.top;
    this.bottom = this.$refs.bottom;
    this.move();
  },
  methods: {
    draw: function() {
      if (this.top && this.bottom) {

        var x = -((this.distance + 50 + (this.index * 70)) % 210 ) + 100;
        this.top.style.left = x + '%';
        this.bottom.style.left = x + '%';
        this.reportPosition();


        if (x < -10) {
          if (this.visible) {
            this.move();
            this.visible = false;
          }
        } else {
          this.visible = true;
        }
        // console.log(element.offsetTop + " " + element.offsetLeft + " " + (element.offsetTop + element.clientHeight))
      }
    },
    move: function() {
      var y = (Math.random() * 70) + 10
      this.top.style.top = (y - 100) + '%';
      this.bottom.style.top = (y + 20) + '%';
    },
    reportPosition: function(){
      var data = {
        id: 'obstacles',
        positions: [{
          id : 'top-' + this.index,
          left : this.top.offsetLeft,
          top : this.top.offsetTop,
          right : this.top.offsetLeft + this.top.clientWidth,
          bottom : this.top.offsetTop + this.top.clientHeight
        },{
          id : 'bottom-' + this.index,
          left : this.bottom.offsetLeft,
          top : this.bottom.offsetTop,
          right : this.bottom.offsetLeft + this.bottom.clientWidth,
          bottom : this.bottom.offsetTop + this.bottom.clientHeight
        }]
      }
      this.$emit('currentPosition', data)
    }

  },
  data () {
    return {
      top: null,
      bottom: null,
      startDistance: 100,
      visible: true
    }
  },
  watch: {
    distance: {
      immediate: true,
      handler: function () {
        this.draw();
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

.top {
  position:absolute;
  height:100%;
  top:0;
}
.bottom {
  height:100%;
  top:0;
}
.part {
  width:10%;
  position:absolute;
  background:#ccc;
  left:0;

}
</style>



