<template>
  <div id="game">
    <div id="stage">
      <div ref="score" id="score">{{ score }}</div>
      <flappy :flappyY="forceValue"  v-on:currentPosition="updatePositions" />
      <obstacle :index="0" v-bind:distance="distance" v-on:currentPosition="updatePositions"></obstacle>
      <obstacle :index="1" v-bind:distance="distance" v-on:currentPosition="updatePositions"></obstacle>
      <obstacle :index="2" v-bind:distance="distance" v-on:currentPosition="updatePositions"></obstacle>
      <div v-if="!running && started" id="gameover-screen">
        <h1>GAME OVER</h1>
        <button id="start-button" ref="start" v-on:click="start" v-on:touchstart="start">START</button>
      </div>
    </div>
    <!-- <div id="bar" ref="bar"></div> -->
    <div id="highscore">HIGHSCORE: {{ this.highscore }}</div>
  </div>
</template>

<script>
import Flappy from '@/components/Flappy'
import Obstacle from '@/components/Obstacle'

export default {
  name: 'stage',
  props: ['forceValue'],
  components: {
    'flappy': Flappy,
    'obstacle': Obstacle
  },
  created: function () {
    // this.start();
    var highscore = parseInt(localStorage.getItem('highscore'))
    if (highscore) {
      this.highscore = highscore;
    }
  },
  methods:  {
    start: function(){
      if (!this.started) {
        this.started = true;
      }
      if (this.running) {
        return;
      }
      this.running = true;
      this.score = 0;
      this.distance = 0
      this.accelerationRate = .5

      this.$nextTick(function() {
        this.frame = requestAnimationFrame(this.update.bind(this));
        this.interval = setInterval(this.updateAcceleration.bind(this), 1000);
      })
    },
    stop: function(){
      cancelAnimationFrame(this.frame);
      clearInterval(this.interval);
      this.running = false;

      if (this.score > this.highscore) {
        this.highscore = this.score;
        localStorage.setItem('highscore', (this.highscore))
      }
    },
    update: function() {
      this.frame = requestAnimationFrame(this.update.bind(this));

      this.distance += this.accelerationRate;
      this.checkCollsions();
    },
    checkCollsions: function() {
      var intersect = function(r1, r2) {
        return !(r2.left > r1.right || r2.right < r1.left || r2.top > r1.bottom || r2.bottom < r1.top);
      };

      for (var key in this.obstaclesPosition) {
        if (!this.obstaclesPosition.hasOwnProperty(key)) continue;
        var obstaclePos = this.obstaclesPosition[key];
        if (intersect(obstaclePos, this.flappyPosition)) {
          this.stop();
        }
      }
    },
    updatePositions: function(data) {
      if (data.id === 'obstacles') {
        for (var i = 0; i < data.positions.length; i++) {
          this.obstaclesPosition[data.positions[i].id] = data.positions[i];
        }
      } else if (data.id === 'flappy') {
        this.flappyPosition = data.position;
      }
    },
    updateAcceleration: function() {
      this.accelerationRate = this.accelerationRate * this.accelerate;
      this.score += 10;
    }

  },
  data () {
    return {
      running: false,
      started: false,
      flappyX: 0,
      distance: 0,
      accelerate: 1.01,
      accelerationRate: .5,
      score: 0,
      highscore: 0,
      frame: null,
      interval: null,
      obstaclesPosition : {},
      flappyPosition : {}
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
#stage {
  background:#333;
  position: relative;
  padding-top:100%;
  max-width: 500px;
  margin:0 auto;
  overflow:hidden;
}
#game {
  max-width: 500px;
  margin:0 auto;
}

#score {
  position:absolute;
  top:10px;
  right:10px;
  color:#fff;
  font-size:30px;
  z-index:20;
}
#gameover-screen {
  background:rgba(0,0,0,.8);
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  z-index:10;
  color:#fff;
  h1 {
    top:20%;
    position:relative;
    text-align:center;
  }
}
#start-button {
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  font-family: 'Cousine', monospace;
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 30px;
  font-weight:bold;
  margin: 4px 2px;
}
#highscore {
  position:absolute;
  bottom:10px;
  left:10px;
  font-size:20px;
  z-index:1;
}
#bar {
  height:20px;
  background:#999;
  width:0;
  transition: .1s width;
}
</style>



